var render = function () {var _vm=this;var _h=_vm.$createElement;var _c=_vm._self._c||_h;return _c('v-container',[_c('h1',[_vm._v("蒂姆·德雷克")]),_c('hr',{staticClass:"mt-3 v-divider theme--light"}),_c('div',{staticStyle:{"display":"flex","flex-direction":"row"}},[_c('div',{staticStyle:{"display":"flex","flex-direction":"column"}},[_c('blockquote',{staticClass:"blockquote"},[_vm._v("        蒂姆·德雷克是一个虚构的超级英雄，出现在DC Comics出版的美国漫画书中，通常与超级英雄蝙蝠侠联合。由马尔夫·沃尔夫曼和帕特·布罗德里克创作，他首次出现在蝙蝠侠＃436（1989年8月）中，担任第三个角色扮演蝙蝠侠的警戒伙伴罗宾。继蝙蝠侠： 2009年兜帽之战后，德雷克采用了红罗宾的别名。截至2019年，蒂姆已经回归了他在神奇漫画重新启动Young Justice时的原始罗宾角色。")]),_c('blockquote',{staticClass:"blockquote",staticStyle:{"margin-top":"-20px"}},[_vm._v("        作为一个小男孩，德雷克在Dick Grayson的父母被谋杀的那天晚上在观众席上，后来通过他们的攻击设法发现了蝙蝠侠和原始罗宾的身份。第二个罗宾，杰森托德去世后，目睹蝙蝠侠陷入黑暗，蒂姆确信他应该训练成为第三个罗宾。")])]),_c('v-img',{staticClass:"stage-img",attrs:{"width":"195","height":"260"}})],1),_c('br'),_c('h2',[_vm._v("创作起源")]),_c('hr',{staticClass:"mt-3 v-divider theme--light"}),_c('div',{staticStyle:{"display":"flex","flex-direction":"row"}},[_c('div',{staticStyle:{"display":"flex","flex-direction":"column"}},[_c('blockquote',{staticClass:"blockquote"},[_vm._v("        蒂姆·德雷克而得名蒂姆·伯顿，当时即将到来的导演1989年的电影，并在1989年的推出蝙蝠侠：第三年与他的出身在详细蝙蝠侠：死亡的一个人迹罕至的地方，[6]中，他第一次把自己介绍给迪克·格雷森并用他的技能给前罗宾留下了深刻的印象。这导致格雷森和后来的布鲁斯韦恩的管家阿尔弗雷德彭尼沃斯支持蒂姆要求成为蝙蝠侠的新伙伴。不想犯与杰森托德一样的错误，蝙蝠侠让蒂姆经历了一段从未给过他的前任的密集训练期。最终，蒂姆从稻草人手中救出了蝙蝠侠，他证明了自己的能力并成为了罗宾。蝙蝠侠编辑丹尼斯奥尼尔希望格雷森批准德雷克会让读者接受他。很明显，这种方法很成功，读者已经接受了这样一个角色：经过三次成功的迷你剧后，这个角色在1993年到2009年期间有了自己的183期系列。Mike Mullins在Newsarama上说过：")]),_c('blockquote',{staticClass:"blockquote",staticStyle:{"margin-top":"-20px"}},[_vm._v("        在整个罗宾系列中，罗宾的角色一直被捕获，这表明他将面临越来越大的挑战。罗宾是一个表现出主动性并且被驱使去做他认为正确的人的角色。他知道他辜负了迪克格雷森留下的遗产，并努力不让布鲁斯韦恩，蝙蝠侠失望。蒂姆是一个比以前的罗宾斯更自然的侦探，并且有计算机的天赋，这使他能够站在自己独特的聚光灯下。与他的前任不同，蒂姆并不是最熟练的战斗员，他不得不真正开展他的战斗技术，占据博士的工作人员给他一个蝙蝠侠不需要的优势。蒂姆几乎总是试图分析一个问题并且考虑到他的对手，但是在必要时已经展示了赢得战斗的能力。")])]),_c('v-card',{staticClass:"card-img"},[_c('v-img',{attrs:{"width":"180","height":"240","src":""}}),_c('v-card-title',{attrs:{"prima